@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700|Open+Sans');
:root{
  --color-primary: #f9004d;
  --color-primary-light: #ffedf2;
  --color-body: #717173;
  --color-heading: #1f1f25;
  --color-secondary: #00d09c;
  --color-tertiary: #030303;
  --color-success: #3eb75e;
  --color-danger: #ff0003;
  --color-border: #e9e9e9;
  --color-border-2: #0000004d;
  --color-lighter: #f8f9fc;
  --color-white: #fff;
  --radius: 4px;
  --border-width: 2px;
  --border-sdfs: #717173;
  --color-extra1: #b1b4c1;
  --h1: 72px;
  --h2: 60px;
  --h3: 40px;
  --h4: 30px;
  --h5: 20px;
  --h6: 16px;
}
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  line-height: 1.5em;
  font-weight: 400;
  background: #191919;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  box-sizing: border-box;
}
a{
  color: #666666;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
  color: black;
}